import Image from "next/image";
import { urlFor } from "@/sanity/lib/image";
import { PAGE_QUERYResult } from "@/sanity/types";
import { stegaClean } from "next-sanity";
import { PortableText } from "next-sanity";
import { useState } from 'react';


type SplitImageProps = Extract<
    NonNullable<NonNullable<PAGE_QUERYResult>["content"]>[number],
    { _type: "splitImage" }
>;

export function SplitImage({ title, image, orientation, description, authorimage, authorname, authordesignation, video
}: SplitImageProps) {
    const [showVideo, setShowVideo] = useState(false);

    const videoUrl = video?.url || null;

    console.log(video);
    return (
        <>
            <section
                className="container w-[1200] mx-auto flex gap-14 py-16 data-[orientation='imageRight']:flex-row-reverse"
                data-orientation={stegaClean(orientation) || "imageLeft"}
            >
                {image ? (
                    <Image
                        className="rounded-xl w-2/3 h-auto bg-white p-8"
                        src={urlFor(image).width(557).height(505).url()}
                        width={557}
                        height={505}
                        alt=""
                    />
                ) : null}
                <div className="w-1/2 flex flex-col gap-4 items-left">
                    {title ? (
                        <h2 className="text-2xl md:text-5xl font-light text-black-800 text-pretty max-w-3xl">
                            {title}
                        </h2>
                    ) : null}
                    <div className="prose-lg lg:prose-xl prose-invert flex items-left">
                        {description ? <PortableText value={description} /> : null}
                    </div>
                    <div className="flex flex-row  p-4 w-3/4">
                        {authorimage ? (
                            <Image
                                className=" h-auto bg-red-200"
                                src={urlFor(authorimage).width(80).height(80).url()}
                                width={80}
                                height={80}
                                alt=""
                            />
                        ) : null}
                        <div className="flex flex-col bg-white w-full p-2">
                            {authorname ? (
                                <h3 className="text-2xl font-semibold text-slate-800">
                                    {authorname}
                                </h3>
                            ) : null}
                            {authordesignation ? (
                                <p className="text-lg text-slate-600">
                                    {authordesignation}
                                </p>
                            ) : null}

                        </div>
                        {videoUrl && (
                            <div className="mt-3  ">
                                <button
                                    onClick={() => setShowVideo(true)}
                                    className="  w-14 h-14 bg-orange-600 rounded-md flex items-center justify-center hover:bg-red-700 transition play-btn"
                                    aria-label="Play Video"
                                >
                                    <div className="icon-pulse-wrapper">
                                        <i className="fas fa-light fa-circle-play text-white text-xl"></i>
                                    </div>

                                </button>
                            </div>
                        )}
                    </div>
                </div>
            </section>
            {/* Video Popup */}
            {
                showVideo && (
                    <div className="fixed inset-0 bg-black bg-opacity-70 flex items-center justify-center z-50">
                        <div className="relative w-full max-w-4xl aspect-video">
                            <button
                                onClick={() => setShowVideo(false)}
                                className="absolute top-2 right-2 z-10 bg-white text-black rounded-full w-8 h-8 flex items-center justify-center hover:bg-gray-200"
                                aria-label="Close Video"
                            >
                                âœ•
                            </button>
                            {videoUrl && (

                                <video controls className="rounded-xl w-full h-auto">
                                    <source src={videoUrl} type="video/mp4" />
                                    Your browser does not support the video tag.
                                </video>
                            )}
                        </div>
                    </div>
                )
            }
        </>
    );
}