import { PAGE_QUERYResult } from "@/sanity/types";
import Image from "next/image";
import { urlFor } from "@/sanity/lib/image";
import clsx from 'clsx';


type LogoListProps = Extract<
    NonNullable<NonNullable<PAGE_QUERYResult>["content"]>[number],
    { _type: "logoList" }
>;

export function LogoList({ title, logos, itemsPerRow, backgroundColor }: LogoListProps) {
    return (
        <section className={`p-12 rounded-2xl text-center w-[1200px] mx-auto my-14`} style={{ backgroundColor: backgroundColor?.hex  }}>
            {title && <h2 className="text-2xl font-semibold mb-6">{title}</h2>}
            <div
                className={clsx(
                    'grid gap-6 items-center justify-center',
                    `grid-cols-${itemsPerRow}`
                )}
            >
                {logos.map((logo) => (
                    <div key={logo._key} className="relative w-full flex gap-16 items-center justify-center">
                        <Image
                            src={urlFor(logo).width(162).height(46).url()}
                            alt={logo.alt || 'Logo'}
                            width={162}
                            height={46}
                            className="object-contain"
                        />
                    </div>
                ))}
                
            </div>
        </section>
    );
}