import { PAGE_QUERYResult } from "@/sanity/types";
import Image from "next/image";
import { urlFor } from "@/sanity/lib/image";

type FeaturesProps = Extract<
    NonNullable<NonNullable<PAGE_QUERYResult>["content"]>[number],
    { _type: "cardSection" }
>;

export function CardSection({ cards, title }: FeaturesProps) {
    return (
        <section className="py-12 w-[1200] mx-auto">
            {title && (
                <h2 className="text-3xl font-bold text-center mb-12">{title}</h2>
            )}
            <div className={`${cards?.length === 1 ? 'flex flex-col md:flex-row items-left gap-6 justify-center' : 'grid grid-cols-1 md:grid-cols-2 gap-10'}`}>
                {cards?.map((card) => (
                    <div
                        key={card._key}
                        className={`relative overflow-hidden rounded-lg p-16 bg-white ${cards.length === 1 ? ' flex flex-col md:flex-row gap-18 items-left' : ''}`}
                    >
                        {/* Background Image (overlay style for multi-card layout) */}
                        {card.bgimage && (
                            <div className="absolute inset-0">
                                <Image
                                    src={urlFor(card.bgimage).width(970).height(397).url()}
                                    alt="Background"
                                    fill
                                    style={{ objectFit: 'cover', backgroundPosition: 'bottom' }}
                                />
                            </div>
                        )}

                        {/* Content */}
                        <div className={`relative z-10 ${cards.length === 1 ? 'flex-shrink-0 w-full md:w-1/4' : ' flex flex-col gap-6 items-center'}`}>
                            {/* Icon */}
                            {card.icon && (
                                <div className="flex items-center justify-center ">
                                    <Image
                                        src={urlFor(card.icon).width(270).height(280).url()}
                                        alt={card.title}
                                        width={270}
                                        height={280}
                                        className="object-contain"
                                    />
                                </div>
                            )}
                        </div>

                        <div
                            className={`relative z-10 ${cards.length === 1 ? 'w-full md:w-3/4 flex flex-col gap-2' : ' flex flex-col gap-6 items-center text-center'}`}
                            style={{ color: card.textColor?.hex || '#ffffff' }}
                        >
                            {/* Title */}
                            {card.title && (
                                <h3 className="text-2xl font-semibold">
                                    {card.title}
                                </h3>
                            )}

                            {/* Text */}
                            {card.text && (
                                <p className="text-lg">
                                    {card.text}
                                </p>
                            )}

                            {/* Button */}
                            {card.btntext && card.btnurl && (
                                <a
                                    href={card.btnurl}
                                    className="w-[200px] mt-4 inline-block bg-orange-600 text-white px-5 py-3 rounded hover:bg-blue-700 transition"
                                >
                                    {card.btntext}
                                </a>
                            )}
                        </div>
                    </div>
                ))}
            </div>

        </section>
    );
}