import { PAGE_QUERYResult } from "@/sanity/types";
import { PortableText } from "next-sanity";

type JobsProps = Extract<
    NonNullable<NonNullable<PAGE_QUERYResult>["content"]>[number],
    { _type: "jobs" }
>;

export function Jobs({ _key, title, text, jobs, btntext, btnurl }: JobsProps) {
    return (
        <section className="container w-[1200] mx-auto flex flex-col gap-8 py-16">
            {title ? (
                <h2 className="text-xl mx-auto md:text-2xl lg:text-5xl font-semibold text-slate-800 text-pretty max-w-3xl">
                    {title}
                </h2>
            ) : null}
            <div className="text-center">
                {Array.isArray(text) ? (
                    <PortableText value={text} />
                ) : text ? (
                    <p>{text}</p>
                ) : null}            </div>
            {Array.isArray(jobs) ? (

                <div className="">
                    {/* Labels outside the loop */}
                    <div className="flex justify-between px-4 py-8 font-bold text-slate-700 text-lg">
                        <span>Position</span>
                        <span>Location</span>
                    </div>
                    <div className=" border-t-2 border-orange-500">

                        {jobs.map((job) => (

                            <details
                                key={job._id}
                                className="group [&[open]]:border-orange-500 transition-colors duration-100 px-4 border-b-2 py-4 "
                                name={_key}
                            >
                                <summary className="text-xl font-semibold text-slate-800 list-none cursor-pointer py-4 flex items-center justify-between">
                                    {job.title}
                                    <span className="transform origin-center transition-transform duration-200">
                                        {job.location ? ` ${job.location}` : null}
                                    </span>
                                </summary>
                                <div className="pb-4 flex flex-row gap-6">
                                    {job.body ? <PortableText value={job.body} /> : null}
                                    <a
                                        href={`/job/${job.slug.current}`} target="_blank" rel="noopener noreferrer"
                                        className="text-black-500 underline decoration-2 decoration-orange-500 basis-[10%]" 
                                    >
                                        Learn more
                                    </a>
                                </div>
                            </details>
                        ))}
                        {/* Button */}
                        {btntext && btnurl && (
                            <a
                                href={btnurl}
                                className="mt-18 inline-block w-full bg-orange-600 text-white px-5 py-3 hover:bg-blue-700 transition text-center"
                            >
                                {btntext}
                            </a>
                        )}
                    </div>
                </div>
            ) : null}
        </section>
    );
}